import { Component, OnInit } from '@angular/core';
import { GroupInfoBean, GroupEvalInfoBean } from './../bean/custom-group.bean';
import { CusOperationHttpService } from './../../http/custom-operation/custom-operation.http.service'
import { Message, ConfirmationService, ConfirmDialogModule } from 'primeng/primeng';
import { Observable } from 'rxjs/Rx';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { CustGroupListBean } from './../bean/custom-group.bean';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';


@Component({
  selector: 'app-household-information-display',
  templateUrl: './household-information-display.component.html',
  styleUrls: ['./household-information-display.component.scss'],
  providers: [ConfirmationService,Commfunc],
})
export class HouseholdInformationDisplayComponent implements OnInit {
  //公共
  tableMesg: any = tableMessage;
  // 标题
  title: string = 'xxx客户群-xxx户';
  title1: string = '基本信息';
  // 营销假数据
  items: any[];
  data1: any = {};
  // bean
  groupInfoBean: GroupInfoBean = new GroupInfoBean();//客户群户信息
  groupEvalInfoBean: GroupEvalInfoBean = new GroupEvalInfoBean();//客户群第三方评价

  // 组件参数
  msgs: Message[] = [];
  first1: any = 0;
  first2: any = 0;
  first3: any = 0;
  // 模态框
  detailDisplay: boolean = false;
  updateValue: any = {};
  detailTitle: string = '第三方评价详情';
  // 传参中间量
  groupId: any = '';
  groupName: any = '';
  data: any = new CustGroupListBean;

  // 码值
  //获取所有码值
code:any;
  relType: any;// = CustomGroupRelType;//关联关系
  idenType: any;// = CustomGroupIdenType;//证件类型
  groupHealthFlag: any;// = GroupHealthFlag;// 健康状况
  groupCreditEval: any;// = GroupCreditEval;// 信用评价
  groupSourceOfIncome: any;// = GroupSourceOfIncome;// 收入来源
  groupFamilyStableFlag: any ;//= GroupFamilyStableFlags;// 家庭关系是否稳定
  groupBadHabits: any;// = GroupBadHabits;// 不良嗜好
  groupCarFlag: any;// = GroupCarFlag;// 车辆状况
  groupIncomeLevel: any;// = GroupIncomeLevel; // 家庭年收入级别
  groupOfferLines: any;// = GroupOfferLinese;// 建议额度
  constructor(
    public httpService: CusOperationHttpService,
    public confirmationService: ConfirmationService,
    public routerInfo: ActivatedRoute,
    public router: Router,
    public commfunc : Commfunc
  ) {
    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.relType = this.code['CustomGroupRelType']; 
    this.idenType = this.code['CustomGroupIdenType'];
    this.groupHealthFlag = this.code['GroupHealthFlag']
    this.groupCreditEval = this.code['groupCreditEval']; 
    this.groupSourceOfIncome = this.code['GroupSourceOfIncome'];
    this.groupFamilyStableFlag = this.code['GroupFamilyStableFlags']
    this.groupBadHabits = this.code['GroupBadHabits']; 
    this.groupCarFlag = this.code['GroupCarFlag'];
    this.groupIncomeLevel = this.code['GroupIncomeLevel']
    this.groupOfferLines = this.code['GroupOfferLinese']; 

   }
//码值
codeValues() {
  this.commfunc.codeValue();
  let ret = this.commfunc.codeDatas;
  return this.code= ret //code是自己声明的变量
  }
  ngOnInit() {
    this.preSet();//初始化
    this.selectGroupInfo();
    this.selectGroupEvalInfo();

    setTimeout(() =>{
      this.items = [];
    })
  }
  preSet() {
    // 初始化
    // 传参
    if (this.routerInfo.snapshot.params.groupId) {
      this.groupId = this.routerInfo.snapshot.params.groupId;
    }
    if (this.routerInfo.snapshot.params.groupName) {
      this.groupName = this.routerInfo.snapshot.params.groupName;
    }
    if (this.routerInfo.snapshot.params.data) {
      let tempData = this.routerInfo.snapshot.params.data;
      this.data.custMap = JSON.parse(tempData);
    }
    //客户群户信息查询
    this.groupInfoBean.pageNum = 1;
    this.groupInfoBean.pageSize = 10;
    // 客户群第三方评价
    this.groupEvalInfoBean.pageNum = 1;
    this.groupEvalInfoBean.pageSize = 10;
    this.title = `${this.groupName}客户群-${this.data.custMap.doorNum}户`;
  }
  selectGroupInfo() {
    // 客户群户信息查询
    this.groupInfoBean.groupId = this.groupId;
    this.groupInfoBean.groupName = this.groupName;
    this.groupInfoBean.groupType = '1';
    this.groupInfoBean.doorId = this.data.custMap.doorNum;
    this.httpService.selectGroupInfo(this.groupInfoBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.groupInfoBean = data;
        this.groupInfoBean.doorList = data.doorList;
      }
      else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '查询失败';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.code }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    });
  }
  selectGroupEvalInfo() {
    // 客户群第三方评价查询
    this.groupEvalInfoBean.groupId = this.groupId;
    this.groupEvalInfoBean.groupName = this.groupName;
    this.groupEvalInfoBean.handleFlag = '2';
    this.groupEvalInfoBean.custNo = this.data.custMap.custNo;
    this.httpService.selectGroupEvalInfo(this.groupEvalInfoBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.groupEvalInfoBean = data;
        this.groupEvalInfoBean.evalList = data.evalList;
      }
      else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '查询失败';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.code }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    });
  }
  showDetail(col) {
    // 弹出模态框
    this.detailDisplay = true;
    this.updateValue = col;
    document.body.style.overflow = "hidden";
  }
  detailsCall(event) {
    this.detailDisplay = event;
    document.body.style.overflow = 'auto';
  }

  doorPaginate(event) {
    // 群户分页
    this.groupInfoBean.pageSize = event.rows;
    let currentPage = event.page + 1;
    this.groupInfoBean.pageNum = currentPage;
    this.first1 = (currentPage - 1) * this.groupInfoBean.pageSize;
    this.selectGroupInfo();//群户查询
  }
  itemPaginate(event) {
    // 营销分页
    this.data1.pageSize = event.rows;
    let currentPage = event.page + 1;
    this.data1.pageNum = currentPage;
    this.first2 = (currentPage - 1) * this.data1.pageSize;
    //营销查询--待修改2018.1.5
  }
  evalPaginate(event) {
    // 第三方评价分页
    this.groupEvalInfoBean.pageSize = event.rows;
    let currentPage = event.page + 1;
    this.groupEvalInfoBean.pageNum = currentPage;
    this.first3 = (currentPage - 1) * this.groupEvalInfoBean.pageSize;
    this.selectGroupEvalInfo();//客户群名单查询
  }
  onHidden() {
    // 退出模态框
    document.body.style.overflow = 'auto';
  }
  // 链接
  returnGroupView() {
    // 返回客户群视图
    this.confirmationService.confirm({
      message: '确定返回客户群视图?',
      accept: () => {
        this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-view', { groupId: this.groupId, groupName: this.groupName }]);
      }
    });
  }
}
